#settings-panel-container {
  visibility: hidden;
}

#settings-panel {
  text-align: center;
  text-shadow: .1vh .1vh .3vh purple;
  flex-flow: column wrap;
  width: 50vmax;
  padding-top: 5vmax;
  font-family: Indie Flower;
  font-size: 5vh;
  display: flex;
}

.snake-text-input {
  background-color: #0000001a;
  border: none;
  width: 50%;
  margin: .8vh 0;
  padding: 0 .4vw 0 .8vw;
  font-family: Indie Flower;
  font-size: 5vh;
  transition: all .1s linear;
}

.snake-text-input:hover {
  box-shadow: .6vh .6vh 1vh #0009;
}

.snake-text-input:focus, .snake-text-input:active {
  background-color: #0000004d;
  box-shadow: .6vh .6vh 1vh #000c;
}

div.settings-buttons {
  text-align: center;
  width: 50%;
  margin-top: 4vh;
  display: inline-block;
}

div.settings-buttons span {
  cursor: pointer;
  transition: all .4s linear;
}

div.settings-buttons span:first-child {
  float: left;
  color: green;
  text-shadow: none;
  margin-left: 2vw;
}

div.settings-buttons span:first-child:hover {
  color: #0f0;
}

div.settings-buttons span:last-child {
  float: right;
  color: #8b0000;
  text-shadow: none;
  margin-right: 2vw;
}

div.settings-buttons span:last-child:hover {
  color: red;
}

#snake-length {
  width: 50%;
}

#saved-stamp {
  z-index: 100;
  color: red;
  font-size: 15vh;
  display: none;
  position: absolute;
  bottom: 50%;
  right: 50%;
  transform: translate(50%, 50%)rotateZ(20deg);
}

@media screen and (width <= 450px) {
  #settings-panel {
    width: 90%;
  }

  .snake-text-input, #snake-length, div.settings-buttons {
    width: 100%;
  }
}

